<template>
  <div>
    <Header title="" :show="!!1"></Header>
    <div class="zhucep">
      <mt-field label="" placeholder="" v-model="username" ref="name"></mt-field>
      <mt-field label="" placeholder="" v-model="usernick"></mt-field>
      <mt-field label="" placeholder="" type="password" v-model="password"></mt-field>
      <mt-field label="" placeholder="" type="password" v-model="password2"></mt-field>
      <mt-field label="" placeholder="" type="tel" v-model="tel"></mt-field>
      <mt-field label="" placeholder="" type="date" v-model="birthday"></mt-field>
      <mt-checklist v-model="value" :options="[' Cookie ']"></mt-checklist>
    </div>
    <div class="foot">
      <mt-button size="small" type="primary" @click="zhuce" class="zhuce"></mt-button>
    </div>
  </div>
</template>
<script>
import { http } from "../utils";
import { Indicator, MessageBox, Toast, Radio,Checklist } from "mint-ui";
import store from "../store";
import { mapState, mapActions } from "vuex";
export default {
  data() {
    return {
      value: ["1"]
    };
  },
  methods: {
    zhuce() {
      var checklist = this.value.length;
      var name = this.$store.state.username;
      var nick = this.$store.state.usernick;
      var pwd1 = this.$store.state.password;
      var pwd2 = this.$store.state.password2;
      var tel = this.$store.state.tel;
      var birthday = this.$store.state.birthday;
      var nameReg = /^[a-zA-Z][a-zA-Z0-9_]{5,15}$/; //
      var nickReg = /[\u4E00-\u9FA5A-Za-z]{2,8}/; //6-8
      var pwdReg = /^[a-zA-Z][a-zA-Z0-9_]{5,15}$/;
      var telReg = /0?(13|14|15|18|17)[0-9]{9}/;
      var birReg = /^\d{4}-\d{1,2}-\d{1,2}/;
      if (
        name != "" &&
        nick != "" &&
        pwd1 != "" &&
        pwd2 != "" &&
        tel != "" &&
        birthday != ""
      ) {
        if (nameReg.test(name)) {
          if (nickReg.test(nick)) {
            if (pwdReg.test(pwd1)) {
              if (pwd1 == pwd2) {
                if (telReg.test(tel)) {
                  if (birReg.test(birthday)) {
                    if (checklist == 2) {
                      this.$store.dispatch("AddUser",{
                        url:"/vue/zhuce",
                        params:{
                          username:name,
                          usernick:nick,
                          password:pwd1,
                          tel:tel,
                          birthday:birthday,
                        },
                        cb:(res)=>{
                          Toast(res.data.msg);
                          this.$router.push({name:"login"})
                        }
                      })
                    } else {
                      Toast("");
                    }
                  } else {
                    Toast("");
                  }
                } else {
                  Toast("");
                }
              } else {
                Toast("");
              }
            } else {
              Toast(",,6-15");
            }
          } else {
            Toast(",6-8");
          }
        } else {
          Toast(",,6-15");
        }
      } else {
        Toast("");
      }
    }
  },
  computed: {
    username: {
      get() {
        return this.$store.state.usrname;
      },
      set(val) {
        this.$store.commit("Changename", val);
      }
    },
    usernick: {
      get() {
        return this.$store.state.usernick;
      },
      set(val) {
        this.$store.commit("Changenick", val); //mutation
      }
    },
    password: {
      get() {
        return this.$store.state.password;
      },
      set(val) {
        this.$store.commit("Changepassword", val);
      }
    },
    password2: {
      get() {
        return this.$store.state.password2;
      },
      set(val) {
        this.$store.commit("Changepassword2", val);
      }
    },
    birthday: {
      get() {
        return this.$store.state.birthday;
      },
      set(val) {
        this.$store.commit("Changebirthday", val);
      }
    },
    tel: {
      get() {
        return this.$store.state.tel;
      },
      set(val) {
        this.$store.commit("Changetel", val);
      }
    }
  }
};
</script>
<style lang="scss" scoped>
.foot {
  text-align: center;
  margin-top: 10%;
  button {
    width: 50%;
    border-radius: 0.4rem;
  }
  .zhuce {
    background-color: #000;
    color: #fff;
  }
}
</style>
